
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import Panel from 'vux/src/components/panel/index.vue'

export default{
name:"Item",
components:{
Swiper,
Marquee,
MarqueeItem,
Panel
},
data(){
return {
list:[],
decList: []
}
},
created(){
this.$jsonp('http://3g.163.com/touch/jsonp/sy/recommend/0-9.html').then((res)=>{

var list = res.list.filter(function(item,index){
return item.picInfo.length !=0
}).map(function(item,index){
return {
url: item.link,
img: item.picInfo[0].url,
title: item.title
}
})
this.list=list;
console.log(res)
var decList = res.news.filter(function(item,index){
return item.picInfo.length !=0
}).map(function(item,index){
return {
src: item.picInfo[0].url,
title: item.title,
desc: item.digest
}
})

this.decList=decList;

});

this.$http.get('../ajax.php?user=小红').then(function(res){
console.log(res)
}).catch(function(res){
console.log('fail'+res)

})
}
}



